<template>
  <form @submit="validarForm">
    <!-- ou @submit.prevent="salvaForm" -->
    <div class="row mt-5">
      <div class="col-sm-12">
        <div class="card shadow">
          <header class="card-header">
            <strong class="align-self-center">
              Crie um conjunto de taréfas para cliente</strong
            >
          </header>
          <div class="card-body">
            <div class="row">
              <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-3">
                <div class="row">
                  <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                    <label v-if="tarefaId">
                      Data de criação {{ ViewModel.dataCriacao }}</label
                    >
                  </div>
                  <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                    <label v-if="tarefaId">
                      Data ultima alteração
                      {{ ViewModel.dataUltimaAlteracao }}</label
                    >
                  </div>
                </div>
                <label> Campos com * são de preenchimento obrigatório </label>

                <div class="row">
                  <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                    <label>* Nome do cliente</label>
                    <input
                      class="form-control"
                      type="text"
                      placeholder="Digite o nome"
                      v-model="ViewModel.nomeCliente"
                      :disabled="inputReadonly"
                    />
                  </div>
                  <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                    <label>* CNPJ</label>
                    <input
                      class="form-control"
                      type="text"
                      placeholder="Digite o cnpj"
                      v-model="ViewModel.cnpj"
                      :disabled="inputReadonly"
                    />
                  </div>
                  <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                    <label>* CEP</label>
                    <input
                      class="form-control"
                      type="text"
                      placeholder="Digite o cnpj"
                      v-model="ViewModel.value"
                      :disabled="inputReadonly"
                    />
                  </div>
                  <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                    <label>* Verba</label>
                    <input
                      class="form-control"
                      type="text"
                      placeholder="Digite o nome"
                      v-model="ViewModel.verba"
                      :disabled="inputReadonly"
                    />
                  </div>
                  <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                    <label>* Tipo:</label>
                    <Multiselect
                      v-model="ViewModel.tipo"
                      placeholder="Selecione o tipo"
                      :options="optionsTipo"
                      :disabled="inputReadonly"
                      :required="true"
                      :multiple="true"
                    />
                  </div>
                  <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                    <label>* Pagamento:</label>
                    <Multiselect
                      v-model="ViewModel.formaPagamento"
                      placeholder="Selecione de pagamento"
                      :options="optionsPagemento"
                      :disabled="inputReadonly"
                      :required="true"
                    />
                  </div>
                </div>

                <div class="d-flex mt-3 buttonsActions">
                  <button
                    v-if="mostrarBtnSalvar"
                    class="btn btn-success"
                    type="submit"
                  >
                    Salvar
                  </button>
                  <input
                    type="button"
                    v-if="mostrarBtnSalvar"
                    class="btn btn-warning ml-2 mr-2"
                    @click="limpaForm()"
                    value="Limpar"
                  />

                  <button
                    class="btn btn-secondary"
                    type="reset"
                    @click="$router.push('/tarefas')"
                  >
                    Voltar
                  </button>
                </div>
              </div>
              <br /><br /><br />
            </div>
          </div>
        </div>       
      </div>
    </div>
  </form>
</template>

<script>
import { ref, onMounted } from "vue";
import Multiselect from "@vueform/multiselect";
import { v4 as uuidv4 } from "uuid";
import Swal from "sweetalert2";
import { /* useRouter, */ useRoute } from "vue-router";
import { useStore } from "vuex";
import servico from "../../requisicoes/servicos/usuarios/index";

export default {
  components: { Multiselect },
  setup() {
    const {
      params: { tarefaId, visualizar },
    } = useRoute();
    const store = useStore();
    const getId = () => {
      return tarefaId ? tarefaId : store.state.emptyGuid;
    };

    let mostrarBtnSalvar;
    let inputReadonly;
    
    // let router = useRouter();
    let optionsTipo = [
      { value: 0, label: "Google" },
      { value: 1, label: "Instagran" },
      { value: 2, label: "Facebook" },
      { value: 3, label: "Site" },
    ];
    let optionsPagemento = [
      { value: 0, label: "Cartão" },
      { value: 1, label: "Boleto" },
    ];
    let optionsUsuarios = ref([]);
    if (visualizar) {
      mostrarBtnSalvar = false;
      inputReadonly = true;
    } else {
      mostrarBtnSalvar = true;
      inputReadonly = false;
    }

    let ViewModel = ref({
      id: ref(getId),
      nomeCliente: ref(),
      cnpj: ref(),
      cep: ref(),
      verba: ref(),
      formaPagamento: ref(),
      tipo: ref(),
    });
    let mensagemChat = ref();
    let chatMensage = ref();

    const limpaForm = () => {
      ViewModel.value.id = ref();
      ViewModel.value.value = ViewModel.value.value + 1;
      ViewModel.value.descricao = ref();
      ViewModel.value.atribuidaPara = ref();
      ViewModel.value.concluida = ref(false);
      ViewModel.value.deadline = ref();
      ViewModel.value.dataCriacao = ref();
      ViewModel.value.dataUltimaAlteracao = ref();
    };

    const validarForm = (e) => {
      e.preventDefault();
      if (ViewModel.value.atribuidaPara) {
        salvaForm();
      } else {
        Swal.fire({
          icon: "warning",
          title: `Atenção`,
          text: `Campos com * são de preenchimento obrigatório`,
        });
      }
    };
    const salvaForm = () => {
      var data = new Date();
      var dia = data.getDate();
      var mes = data.getMonth();
      let ano = data.getFullYear();
      var hora = data.getHours();
      var min = data.getMinutes();
      if (tarefaId) {
        let data = {
          id: ViewModel.value.id,
          value: ViewModel.value.value,
          atribuidaPara: ViewModel.value.atribuidaPara,
          descricao: ViewModel.value.descricao,
          concluida: ViewModel.value.concluida,
          dataUltimaAlteracao:
            dia + "/" + mes + "/" + ano + ":" + hora + ":" + min,
        };
        let url = "tarefa/" + ViewModel.value.idBanco;
        servico
          .edit(url, data)
          .then(() => {
            Swal.fire({
              icon: "success",
              title: `Concluído`,
              text: `O item "${ViewModel.value.nome}" foi adicionado `,
            });

            status200Edt();
          })
          .catch((error) => {
            console.error(error);
          });
      } else {
        ViewModel.value.id = uuidv4();
        let data = {
          id: ViewModel.value.id,
          value: ViewModel.value.value,
          atribuidaPara: ViewModel.value.atribuidaPara,
          descricao: ViewModel.value.descricao,
          concluida: ViewModel.value.concluida,
          deadline: ViewModel.value.deadline,
          dataCriacao: dia + "/" + mes + "/" + ano + ":" + hora + ":" + min,
          dataUltimaAlteracao:
            dia + "/" + mes + "/" + ano + ":" + hora + ":" + min,
          chat: ViewModel.value.chat,
        };
        console.log("data", data);
        servico
          .add("tarefa", data)
          .then(() => {
            Swal.fire({
              icon: "success",
              title: `Concluído`,
              text: `O item "${ViewModel.value.nome}" foi adicionado `,
            });
            status200();
            limpaForm();
          })
          .catch((error) => {
            console.error(error);
          });
      }
    };
    const status200 = () => {
      Swal.fire({
        icon: "success",
        title: `Concluído`,
        text: `A tarefa "${ViewModel.value.value}" foi adicionada`,
      });
      limpaForm();
    };
    const status200Edt = () => {
      Swal.fire({
        icon: "success",
        title: `Concluido`,
        text: `A tareda "${ViewModel.value.value}" foi editada`,
      });
    };



   


    onMounted(async () => {
      const ObterTarefa = async (id) => {
        console.log("id", id);
        servico
          .get("tarefa")
          .then((snapshot) => {
            if (snapshot.exists()) {
              // console.log(snapshot.val());
              let data = snapshot.val();
              // console.table(data);
              for (var [key, value] of Object.entries(data)) {
                if (id == value.id) {
                  ViewModel.value.id = value.id;
                  ViewModel.value.atribuidaPara = value.atribuidaPara;
                  ViewModel.value.descricao = value.descricao;
                  ViewModel.value.value = value.value;
                  ViewModel.value.idBanco = key;
                  ViewModel.value.concluida = value.concluida;
                  ViewModel.value.deadline = value.deadline;
                  ViewModel.value.dataCriacao = value.dataCriacao;
                  ViewModel.value.dataUltimaAlteracao =
                    value.dataUltimaAlteracao;
                  ViewModel.value.dataUltimaAlteracao =
                    value.dataUltimaAlteracao;
                }
              }
            } else {
              console.log("No data available");
            }
          })
          .catch((error) => {
            console.error(error);
          });
      };
      try {
        if (tarefaId !== undefined) {
          ObterTarefa(tarefaId);
        
       
        }
      } catch (err) {
        console.error("Erro ao obter usuario para edição", err);
      }
    });
    return {
      ViewModel,
      optionsUsuarios,
      limpaForm,
      validarForm,
      mostrarBtnSalvar,
      inputReadonly,
      tarefaId,
      mensagemChat,     
      chatMensage,
      optionsTipo,
      optionsPagemento,
    };
  },
};
</script>
<style src="@vueform/multiselect/themes/default.css"></style>
    
<style>
.chat {
  max-height: 400px;
  overflow-y: scroll;
}
.containerChat {
  border: 2px solid #dedede;
  background-color: #f1f1f1;
  border-radius: 5px;
  padding: 10px;
  margin: 10px 0;
}
/* Darker chat container */

.darker {
  border-color: #ccc;
  background-color: #ddd;
}
/* Clear floats */

.container::after {
  content: "";
  clear: both;
  display: table;
}
/* Style images */

.container img {
  float: left;
  max-width: 60px;
  width: 100%;
  margin-right: 20px;
  border-radius: 50%;
}
/* Style the right image */

.container img.right {
  float: right;
  margin-left: 20px;
  margin-right: 0;
}
/* Style time text */

.time-right {
  float: right;
  color: #aaa;
}
/* Style time text */

.time-left {
  float: left;
  color: #999;
}
</style>
